
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
  directives: {
    TransferDom
  },
  components: {
    Confirm,
    Group,
    XInput,
    XButton,
    Toast
  },
  data () {
    return {
      tips:"",
      show: false,
      showPositionValue:false,
      bindmobile:true
    }
  },
  mounted(){
    // this.$http({
    //         method:'POST',
    //         url:this.$store.state.domain+'api/WechatAPI/IsBindCustomerPhone',
    //         body:{},
    //         headers: {"Authorization": this.$store.state.jwt.token}
    //         })
    //     .then((d) => {
    //             if(!d.data.success){
    //               this.showPosition(d.data.message);
    //               this.$router.push("/bindmobile")
    //               return false;
    //             }else{
    //               if(d.data.content){
                   
    //               }else{
    //                 this.bindmobile=false;
    //               }
    //             }
    //           }, (d) => {
    //             console.log('');
    //     });
  },
  methods: {
    showPosition (t) {
      this.showPositionValue = true
      this.tips=t;
    },
    onCancel () {
      this.show=false;
    },
    onSubmit(ev){
        this.show=true;
    },
    onConfirm(msg) {
       this.$http({
                    method:'POST',
                    url:this.$store.state.domain+'api/WechatAPI/SetIsDeleteBindCustomerPhone',
                    body:{},
                    headers: {"Authorization": this.$store.state.jwt.token}
                    })
        .then((d) => {
                if(!d.data.success){
                  this.showPosition(d.data.message);
                  return false;
                }else{
                  this.showPosition('!');
                  this.$router.push("/bindmobile");
                }
              }, (d) => {
                this.showPosition('');
        });
      this.show=false;
    }
  }
}



